<template>
  <div>
      <div id="logq">
      <div id="loginx">
            <div class="reg-tit">
                <router-link to="/my"><i class="reg-icon"></i></router-link>
                
            </div>
            <div id="lgx-text">
                <div class="lg-leftv" @click="lxShow" ></div>
                <div class="lg-rightv" @click="lxShow1"></div>
            </div>
        </div>
        <div id="login" v-show="!regShow">
            <div class="log-main">
                <div class="lgname">
                    <span></span>
                    <input type="text" placeholder="//">
                    <i class="icon-del"></i>
                </div>
                <div class="lgname">
                    <span></span>
                    <input type="password" placeholder="">
                    <i class="icon-del1"></i>
                </div>
                <div class="lg-btn"> </div>
                <div class="lg-btw"></div>
                <div class="lg-tip">
                    <span>
                        <i class="tip-left"></i>
                        <span></span>    
                    </span>
                    <span>
                        <i class="tip-right"></i>
                        <span></span>    
                    </span>
                </div>
            </div>
        </div>
      <div id="regist" v-show="regShow">
          <div class="reg-main">
                <el-form id="regFrm" :status-icon="true" :rules="rules" label-position="left" action="" method="" ref="form" :model="form"
                    label-width="70px">
                    <el-form-item label="" prop="username">
                        <el-input v-model="form.username" name="username"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="password">
                        <el-input type="password" v-model="form.password" name="password"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="password2">
                        <el-input type="password" v-model="form.password2"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="email">
                        <el-input v-model="form.email" name="email"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="phone">
                        <el-input v-model="form.phone" name="phone"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button id="reg-btn" @click="regist()" type="primary"></el-button>
                    </el-form-item>
                </el-form>
            </div>
      </div>
      <div class="lg-space">
        <h4></h4>
        <i class="other"></i>
        <p></p>
    </div>
    </div>

    <div class="agreement-dialog" v-show="covershow">
    <div class="agreement-body">
        <p class="agreement-title"></p>
        <div class="agreement-line clearfix"></div>
        <div class="agreement-content">
            <p><span class="important-tip"></span></p>
            <p class="link-top"><a href=""></a></p>
            <p class="link-content"><a href=""></a></p>
            <p class="link-bottom"><a href=""></a></p>
            <p><span class="important-tip"></span></p>

        </div>
        <div class="agreement-btn-wrap">
            <p class="agreement-notice"><span><a href=""></a><a href=""></a><a href=""></a></span></p>
            <div class="agreement-btns">
                <a href="#/registx" class="agreement-btn btn-cancel agreement-btn-cancel clearfix"></a>
                <div @click="covershows" class="agreement-btn btn-complete clearfix"></div>
            </div>
        </div>
    </div>
</div>
  </div>
</template>
<script>
export default {
  data() {
    var _this = this;
    var chkPwd = function(rule, value, callback) {
      // console.log(_this)//
      // console.log(this)
      if (value.trim() == "") {
        callback(new Error(""));
      } else if (value == _this.form.password) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    return {
      regShow: false,
      regShow1: true,
      covershow:true,
      form: {
        username: "",
        password: "",
        password2: "",
        email: "",
        phone: "",
        sex: "1"
      },
      rules: {
        //
        username: [
          {
            validator: function(rule, value, callback) {
              var usernameReg = /^[a-z]\w{2,17}$/;
              if (value.trim() == "") {
                callback(new Error(""));
              } else if (!usernameReg.test(value)) {
                callback(new Error("3-18"));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        password: [
          {
            validator: function(rule, value, callback) {
              var len = value.length;
              if (value.trim() == "") {
                callback(new Error(""));
              } else if (len < 6 || len > 18) {
                callback(new Error("6-18"));
              } else {
                //password2
                //validateField 
                _this.$refs.form.validateField("password2");
                callback();
              }
            }
            //change
          }
        ],
        password2: [
          {
            validator: chkPwd
          }
        ],
        email: [
          {
            validator: function(rule, value, callback) {
              var emailReg = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
              if (value.trim() == "") {
                callback(new Error(""));
              } else if (!emailReg.test(value)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        phone: [
          {
            validator: function(rule, value, callback) {
              var phonereg = /^[1][3,4,5,7,8][0-9]{9}$/;
              if (value.trim() == "") {
                callback(new Error(""));
              } else if (!phonereg.test(value)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    lxShow() {
      console.log(this);
      this.regShow = false;
      this.jQuery(".lg-leftv").css({ borderBottom: "2px solid #F23030" });
      this.jQuery(".lg-rightv").css({ borderBottom: "2px solid #fff" });
    },
    lxShow1() {
      this.regShow = true;
      this.jQuery(".lg-leftv").css({ borderBottom: "2px solid #fff" });
      this.jQuery(".lg-rightv").css({ borderBottom: "2px solid #F23030" });
    },
    covershows(){
        this.covershow=false
    }
  }
};
</script>
<style>
* {
  padding: 0px;
  margin: 0px;
  font-size: 14px;
  box-sizing: border-box;
  
}
p{
    margin-bottom:0;
}
#logq {
  background: #fff;
}
body {
  font-family: PingFang-SC-Regular;
}
.log-main {
  width: 90%;
  margin: 0 auto;
}

.lgname {
  border-bottom: 1px solid #cccccc;
  height: 48px;
  line-height: 48px;
  position: relative;
  margin-bottom: 5px;
  padding: 0 5px;
}
.lgname span {
  color: #333333;
}
.lgname input {
  border: none;
  outline: none;
  margin-left: 30px;
  width: 70%;
  height: 90%;
  font-family: PingFang-SC-Regular;
}
.lgname input::-webkit-input-placeholder {
  color: #ccc;
}
.icon-del {
  width: 22px;
  height: 22px;
  overflow: hidden;
  position: absolute;
  right: 10px;
  top: 12px;
  background-image: url(/static/img/iconBg.png);
  background-repeat: no-repeat;
  background-size: 100% auto;
  display: none;
}
.icon-del1 {
  width: 30px;
  height: 18px;
  overflow: hidden;
  position: absolute;
  right: 10px;
  top: 15px;
  background-image: url(/static/img/password.png);
  background-repeat: no-repeat;
  background-size: 100% auto;
}
.lg-btn {
  height: 45px;
  line-height: 46px;
  background-color: #eee;
  border-radius: 2px;
  text-align: center;
  color: #848689;
  margin-top: 30px;
}
.lg-btw {
  border: 1px solid #f23030;
  color: #f23030;
  background: #fff;
  margin-top: 10px;
  border-radius: 2px;
  text-align: center;
  height: 45px;
  line-height: 46px;
}
.lg-tip {
  margin-top: 20px;
}
.lg-tip span {
  font-size: 12px;
}
.lg-tip > span:last-child {
  float: right;
}
.lg-tip span span {
  margin-left: 26px;
}
.tip-left {
  width: 22px;
  height: 22px;
  display: inline-block;
  position: absolute;
  background: url(/static/img/iconBg.png) 0 -22px;
  background-repeat: no-repeat;
  background-size: 100% auto;
}
.tip-right {
  width: 22px;
  height: 22px;
  display: inline-block;
  position: absolute;
  background: url(/static/img/iconBg.png) 0 -42px;
  background-repeat: no-repeat;
  background-size: 100% auto;
}
.lg-space {
  margin-top: 105px;
  height: 200px;
  position: relative;
  padding: 24px 10px 0 10px;
  border-top: 1px solid #d7d7d7;
}
.lg-space h4 {
  position: absolute;
  top: -12px;
  left: 50%;
  background-color: white;
  padding: 0 10px;
  color: #bfbfbf;
  transform: translateX(-50%);
}
.other {
  margin: 20px auto;
  display: block;
  width: 22px;
  height: 22px;
  background: url(/static/img/iconBg.png) 0 -128px;
  background-repeat: no-repeat;
  background-size: 100% auto;
}
.lg-space p {
  text-align: center;
}

.reg-main {
  width: 90%;
  margin: 10px auto;
}
#reg-btn {
  width: 335px;
  margin-left: -70px;
}
.reg-tit {
  height: 45px;
  line-height: 45px;
  background: #f9f9f9;
  color: #616161;
  font-size: 17px;
  border-bottom: 1px solid #bbb;
  text-align: center;
  /* z-index: 999; */
}
.reg-icon {
  background: url(/static/img/iconBg.png) 0 -61px;
  width: 22px;
  height: 22px;
  display: inline-block;
  background-repeat: no-repeat;
  background-size: 100% auto;
  position: absolute;
  top: 10px;
  left: 12px;
}
#lgx-text {
  line-height: 44px;
  font-size: 14px;
  border-bottom: 1px solid #d7d7d7;
  display: flex;
  justify-content: space-around;
}
.lg-rightv {
  padding: 0 20px;
  height: 100%;
  line-height: 44px;
}
#lgx-text .lg-leftv {
  padding: 0 20px;
  height: 100%;
  line-height: 44px;
  border-bottom: 2px solid #f23030;
}
/* .lgx-text div:hover,.lgx-text div:active{
     border-bottom: 2px solid #F23030;
  }  */

  .agreement-dialog {
    top: 0;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 1000;
    background: rgba(0,0,0,.5);
    font-family: PingFangSC-Regular;
    overflow: hidden;
}
.agreement-body {
    position: absolute;
    bottom: 0;
    padding: 0 10px;
    background: #f8f8f8;
    width: 100%;
    overflow: hidden;
    border-radius: 3px;
    box-sizing: border-box;
    height:367px;
}
.agreement-title {
    width: 100%;
    font-weight: bold;
    font-size: 18px;
    padding: 12px 0;
    text-align: center;
}
.agreement-line {
    border-top: 1px solid #d7d7d7;
    width: 104%;
    float: left;
    margin-left: -10px;
}
.agreement-content {
    width: 100%;
    height: 190px;
    font-size: 13px;
    padding-top: 12px;
    color: black;
    overflow-y: scroll;
    overflow-x: hidden;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    text-align: left;
}
.agreement-content p {
    font-size: 13px;
    line-height: 18px;
}
.agreement-btn-wrap {
    left: 0;
    bottom: 0;
    width: 100%;
    background: #f8f8f8;
    box-sizing: border-box;
}
.agreement-notice {
    text-align: left;
    font-size: 13px;
    padding: 10px 5px 0 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
.agreement-notice span {
    font-weight: bold;
    line-height: 13px;
}
.agreement-btns {
    height: 55px;
    font-size: 16px;
    text-align: center;
}
.btn-cancel {
    float: left;
    color: #000;
    margin-left: 10px;
    background: #fff;
    border: 1px solid #d7d7d7;
}
.btn-complete {
    color: #fff;
    float: right;
    margin-right: 10px;
    border: 1px solid #E93B3D;
    background: #E93B3D;
}
.agreement-btn {
    display: inline-block;
    text-align: center;
    width: 40%;
    line-height: 39px;
    margin: 8px 0;
    border-radius: 3px;
}
.important-tip {
    font-weight: bold;
    text-decoration: underline;
}
.agreement-content a {
    color: #000;
}
.agreement-notice a {
    text-decoration: underline;
    color: #E93B3D;
}
</style>




